import s from '@m/Footer.module.scss';
import { ThemeContext } from 'context';
import { useContext } from 'react';
import { Link } from 'react-router-dom';
import { HashLink } from 'react-router-hash-link';

import twitter from '@/assets/images/social/twitter.svg';
import telegram from '@/assets/images/social/telegram.svg';
import instagram from '@/assets/images/social/instagram.svg';
import github from '@/assets/images/social/github.svg';

function SectionItem({ children }) {
  return (
    <li className={s['section-item']}>
      {children}
    </li>
  )
}

function Footer() {
  const themeContext = useContext(ThemeContext);

  const goUp = () => {
    window.scrollTo(0, 0);
  };

  return (
    <footer className={s['Footer']}>

      <div className={s['sections']}>

        {/* Firsts Section */}
          <div className={s['section']}>
            <div className={s['ball']} onClick={goUp}>
              <i
                className={`fas fa-angle-up ${s['icon-up']} ${s[themeContext.theme]}`}
              ></i>
            </div>
            <div className={s['section-1-title']}>
              frontendDaler
            </div>
            <div className={s['section-1-subtitle']}>
              Professional web developer
            </div>
          </div>
        {/* Firsts Section */}

        {/* Section Section */}
          <div className={s['section']}>
            <div className={s['section-title']}>Explore</div>
            <ul className={s['section-list']}>
              <SectionItem>
                <Link to="/home">
                  Home
                </Link>
              </SectionItem>
              <SectionItem>
                <Link to="/portfolio">
                  Projects
                </Link>
              </SectionItem>
              {/* <SectionItem>
                <Link to="/questions">
                  Questions
                </Link>
              </SectionItem> */}
            </ul>
          </div>
        {/* Section Section */}

        {/* Third Section */}
          <div className={s['section']}>
            <div className={s['section-title']}>Main</div>
            <ul className={s['section-list']}>
              <SectionItem>
                <HashLink to="/home#skills">
                  Skills
                </HashLink>
              </SectionItem>
              <SectionItem>
                <HashLink to="/portfolio#projects">
                  Projects
                </HashLink>
              </SectionItem>
              {/* <SectionItem>
                <HashLink to="/home#skills">
                  Contact me
                </HashLink>
              </SectionItem> */}
            </ul>
          </div>
        {/* Third Section */}

        {/* Fourth Section */}
          <div className={s['section']}>
            <div className={s['section-title']}>Phone numbers</div>
            <ul className={s['section-list']}>
              <SectionItem>
                +998 91 400-74-39
              </SectionItem>
              <SectionItem>
                +998 91 406-00-49
              </SectionItem>
              <SectionItem>
                +998 90 711-30-17
              </SectionItem>
            </ul>
          </div>
        {/* Fourth Section */}

      </div>

      <ul className={s['social']}>
        <li className={s['social-item']}>
          <a href="https://twitter.com/SaidovDaler" target="_blank">
            <img src={twitter} alt="Wait" className={s['social-img']} />
          </a>
        </li>
        <li className={s['social-item']}>
          <a href="https://github.com/dalerfrontend" target="_blank">
            <img src={github} alt="Wait" className={s['social-img']} />
          </a>
        </li>
        <li className={s['social-item']}>
          <a href="https://www.instagram.com/dalerfrontend/?hl=ru" target="_blank">
            <img src={instagram} alt="Wait" className={s['social-img']} />
          </a>
        </li>
      </ul>

    </footer>
  )
}

export default Footer
