import s from '@m/Home/SkillsSection.module.scss';

import reactImg from '@/assets/images/skills/react.svg';
import vueImg from '@/assets/images/skills/vue.svg';
import gitImg from '@/assets/images/skills/git.svg';
import figmaImg from '@/assets/images/skills/figma.svg';
import firebaseImg from '@assets/images/skills/firebase.svg';

import StyledTitle from 'components/styled/StyledTitle';
import StyledSection from 'components/styled/StyledSection';

function Skill({children, title, imgRef}) {
  return (
    <li className={s['skill']}>
      <div className={s['img-container']}>
        <img
          src={imgRef}
          alt="Wait"
          className={s['img']}
        />
        <h3 className={s['skill-header-title']}>
          {title}
        </h3>
      </div>
        <div className={s['skill-body']}>
          <h3 className={s['skill-title']}>
            {title}
          </h3>
        <div className={s['skill-info']}>
          {children}
        </div>
      </div>
    </li>
  );
}

function SkillsSection() {
  return (
    <StyledSection className={s['SkillsSection']} isTop={false} id="skills">

      {/* Title */}
        <StyledTitle isBald={true}>
          Skills
        </StyledTitle>
      {/* Title */}

      {/* Skills */}
        <ul className={s['skills']}>

          <Skill title={'React Js'} imgRef={reactImg}>
            <ul>
              <li>
                <span className={s['info-grey']}>
                  Experience: 
                </span> 1+ years
              </li>
              <li>
                <span className={s['info-grey']}>
                  Related tecknologies
                </span> Redux, Styled components, React Router Dom
              </li>
            </ul>
          </Skill>

          <Skill title={'Vue Js'} imgRef={vueImg}>
            <ul>
              <li>
                <span className={s['info-grey']}>
                  Experience: 
                </span> 1+ years
              </li>
              <li>
                <span className={s['info-grey']}>
                  Related tecknologies
                </span> VueX, Vue Router, Vuetify
              </li>     
            </ul>
          </Skill>

          <Skill title={'Git'} imgRef={gitImg}>
            <ul>
              <li>
                <span className={s['info-grey']}>
                  Experience: 
                </span> 1+ years
              </li>
              <li>
                <span className={s['info-grey']}>
                  Related tecknologies
                </span> GitHub
              </li>     
            </ul>
          </Skill>

          <Skill title={'Firebase'} imgRef={firebaseImg}>
            <ul>
              <li>
                <span className={s['info-grey']}>
                  Experience: 
                </span> 3+ months
              </li>
            </ul>
          </Skill>

          <Skill title={'Figma'} imgRef={figmaImg}>
            <ul>
              <li>
                <span className={s['info-grey']}>
                  Experience: 
                </span> 6+ months
              </li>    
            </ul>
          </Skill>

        </ul>
      {/* Skills */}

    </StyledSection>
  )
}

export default SkillsSection
