import React, { useContext, useState } from 'react'
import s from '@m/Settings.module.scss'
import { LangContext, SettingsContext, ThemeContext } from '../context'
import { v4 } from 'uuid';
import Shade from './Shade';

function Theme({ index, selected, onChangeTheme }) {
  return (
    <li
      className={`${s['theme']} ${s[ selected === index ? 'selected' : '' ]}`}
      onClick={onChangeTheme}
    > 
      <div className={s['theme-outline']}></div>
    </li>
  )
}

function Settings(props) {
  const [selected, setSelected] = useState(1);
  const themeContext = useContext(ThemeContext);
  const langContext = useContext(LangContext);

  const handleChangeTheme = (index) => {
    setSelected(index);
    themeContext.set(`theme-${index}`);
  };

  const handleChangeLang = (to) => {
    langContext.set(to);
  };

  const handleShadeClick = () => {
    props.settings.close();
  };

  const renderedThemes = [];

  for (let i = 1; i <= 8 ; i++) {
    renderedThemes.push((
      <Theme
        key={v4()}
        index={i}
        selected={selected}
        onChangeTheme={() => handleChangeTheme(i)}
      />
    ));
  }

  return (
    <>
      <Shade
        onShadeClick={handleShadeClick}
        isVisible={props.settings.opened}
      />
      <div
        className={`${s['Settings']} ${s[props.settings.opened ? 'opened' : 'closed']}`}
      >

        <div className={s['title']}>
          Settings
        </div>

        {/* Theme section */}
          <div className={s['sub-title']}>
            Select theme
          </div>

          <ul className={s['themes']}>
            {renderedThemes}
          </ul>
        {/* Theme section */}

        {/* Language section */}
          {/* <div className={s['sub-title']}>
            Select language
          </div>

          <ul className={s['languages']}>
            <li
              className={`${s['language']} ${s[langContext.lang === 'en' ? 'selected' : '']}`}
              onClick={() => handleChangeLang('en')}
            >
              English
            </li>
            <li
              className={`${s['language']} ${s[langContext.lang === 'ru' ? 'selected' : '']}`}
              onClick={() => handleChangeLang('ru')}
            >
              Russian
            </li>
          </ul> */}
        {/* Language section */}

      </div>
    </>
  )
}

export default Settings
