import s from '@m/Loading.module.scss';

function Loading() {
  const hide = (e) => {
    e.target.style.display = 'none';
  };

  return (
    <div
      onAnimationEnd={hide}
      className={`${s['Loading']}`}
    >
      <div className={s['circle']}></div>
    </div>
  )
}

export default Loading
