import s from '@m/Home/AboutSection.module.scss';
import { ThemeContext } from 'context';
import { useContext } from 'react';
import StyledTitle from '@c/styled/StyledTitle';
import StyledSection from 'components/styled/StyledSection';
import avatar from '@/assets/images/avatar.png';
import { v4 } from 'uuid';

function Item({ children }) {
  return (
    <li className={s['item']}>
      <i className={`fas fa-check ${s['icon-check']}`}></i>
      <span className={s['item-text']}>{children}</span>
    </li>
  )
}

function AboutSection() {
  const themeContext = useContext(ThemeContext);

  const items = [
    '17 years old',
    'Studying in Inha university(IT faculty)',
    'Living in Tashkent',
    'Favourite tecknologies: React, Firebase, Css modules',
    'Languages: Russina(fluent) English(IELTS 7)',
    'Hobbies: Books, Games, Tennis'
  ];

  return (
    <StyledSection className={s['AboutSection']} isTop={true}>
      <StyledTitle isBald={true}>About me</StyledTitle>
      <div className={s['content']}>
        {/* Left */}
          <div className={s['left']}>

            {/* Image */}
              <div className={`${s['img-container']} ${s[themeContext.theme]}`}>
                <img className={s['img']} src="https://dalerfrontend.github.io/resume/images/avatar.jpg" />
              </div>
            {/* Image */}

            {/* Title */}
              <h3 className={s['img-title']}>
                Hi! I'm Daler
              </h3>
              <h4 className={s['img-subtitle']}>
                frontend developer
              </h4>
            {/* Title */}

            {/* About text */}
              <div className={s['text']}>
                Through constant learning
                and practicing I posses skills
                and abilities in different web
                tecknologies, inluding React,
                Vue and Firebase
              </div>
            {/* About text */}

          </div>
        {/* Left */}

        {/* Right */}
          <ul className={s['right']}>
            {items.map(item => <Item key={v4()}>{item}</Item>)}
          </ul>
        {/* Right */}
      </div>
    </StyledSection>
  )
}

export default AboutSection
