import s from '@m/Portfolio/Project.module.scss';
import propTypes from 'prop-types';

function Project({ title, imgRef, children, codeRef, siteRef }) {
  return (
    <div className={s['Project']}>
      
      {/* Img container */}
        <div className={s['img-container']}>
          <img src={imgRef} className={s['img']} />
        </div>
      {/* Img container */}

      {/* Body */}
        <div className={s['body']}>

          {/* Title */}
            <h3 className={s['title']}>{title}</h3>
          {/* Title */}

          {/* Text */}
            <p className={s['text']}>
              {children}
            </p>
          {/* Text */}

          {/* Buttons */}
            <div className={s['buttons']}>
              <a href={siteRef} target="_blank"  className={s['view-site-btn']}>
                View site
              </a>
              <a href={codeRef} target="_blank" className={s['view-code-btn']}>
                View code
              </a>
            </div>
          {/* Buttons */}

        </div>
      {/* Body */}

    </div>
  )
}

Project.propTypes = {
  title: propTypes.string.isRequired,
  children: propTypes.string.isRequired,
  imgRef: propTypes.string.isRequired,
  codeRef: propTypes.string.isRequired,
  siteRef: propTypes.string.isRequired
};

export default Project
