import s from '@m/Vote.module.scss';
import { useContext, useState } from 'react';
import StyledTitle from './styled/StyledTitle';
import { vote } from '@/firebase/features';
import { AlertContext, ShadeContext } from 'context';
import StyledSection from './styled/StyledSection';

function Star({ index, stars, onSelectStar }) {
  const filled = stars >= index;

  return (
    <li className={s['star']} onClick={() => onSelectStar(index)}>
      {filled ?
        <i className={`fas fa-star ${s['icon-star']} ${s['filled']}`}></i>
          :
        <i className={`far fa-star ${s['icon-star']}`}></i>
      }
    </li>
  );
}

function Vote() {
  const [inputValue, setInputValue] = useState('');
  const [error, setError] = useState(false);
  const [stars, setStars] = useState(2);
  const alertContext = useContext(AlertContext);

  const handleVote = (e) => {
    e.preventDefault();
    if (inputValue.trim() !== '') {
      vote(inputValue, stars);
      setInputValue('');
      setError(false);
      alertContext.show();
    } else {
      setError(true);
    }
  };

  const handleValueChanged = (e) => {
    setInputValue(e.target.value);
  };

  const selectStars = (index) => {
    setStars(index);
  };

  return (
    <StyledSection className={s['Vote']} isTop={false}>

      {/* Title */}
        <StyledTitle>Did you like my resume?</StyledTitle>
      {/* Title */}

      {/* Form */}
        <form className={s['form']} onSubmit={handleVote}>
          <ul className={s['stars']}>
            <Star index={1} stars={stars} onSelectStar={selectStars} />
            <Star index={2} stars={stars} onSelectStar={selectStars} />
            <Star index={3} stars={stars} onSelectStar={selectStars} />
            <Star index={4} stars={stars} onSelectStar={selectStars} />
            <Star index={5} stars={stars} onSelectStar={selectStars} />
          </ul>
          <input
            className={`${s['input-name']} ${s[error ? 'error' : '']}`}
            value={inputValue}
            onChange={handleValueChanged}
            placeholder="Your name..."
          />
          <input
            type="submit"
            value="Submit"
            className={`${s['submit']}`}
          />
        </form>
      {/* Form */}

    </StyledSection>
  )
}

export default Vote
