import { db } from './app';

function vote(from, stars) {
  db.collection('votes')
    .add({ from, stars })
    .then(r => console.log(r))
    .catch(e => console.log(e));
}

function sendMessage(message) {
  db.collection('messages')
    .add(message)
    .then(r => console.log(r))
    .catch(e => console.log(e));
}

export { vote, sendMessage };
