import s from '@m/Home/HireSection.module.scss';
import webStudioImg from '@/assets/images/illustrations/web-studio.svg';
import StyledTitle from 'components/styled/StyledTitle';
import StyledSection from 'components/styled/StyledSection';
import { v4 } from 'uuid';

const reasons = [
  'I am responsible',
  "I'm a good team worker",
  'I am willing to learn new things',
  'I am a good problem solver'
];

function Reason({ children }) {
  return (
    <li className={s['reason']}>
      <i className={`fas fa-fire ${s['icon-fire']}`}></i>
      <span className={s['reason-text']}>{children}</span>
    </li>
  );
}

function HireSection() {
  return (
    <StyledSection className={s['HireSection']} isTop={false}>
      
      {/* Title */}
        <StyledTitle isBald={true}>
          Reasons to choose me
        </StyledTitle>
      {/* Title */}

      {/* Content */}
        <div className={s['content']}>
          <div className={s["img-container"]}>
            <img src={webStudioImg} alt="Wait" className={s['img']} />
          </div>
          <ul className={s['reasons']}>
            {reasons.map(reason => (
              <Reason key={v4()}>
                {reason}
              </Reason>
            ))}
          </ul>
        </div>
      {/* Content */}

    </StyledSection>
  )
}

export default HireSection
