import s from '@m/pages/Portfolio.module.scss';
import StyledTitle from 'components/styled/StyledTitle';
import StyledSection from 'components/styled/StyledSection';
import Projects from 'components/Portfolio/Projects';

function Portfolio() {
  return (
    <StyledSection className={s['Portfolio']} isTop={true}>
      <StyledTitle isBald={true} id="projects">
        Projects
      </StyledTitle>
      <Projects />
    </StyledSection>
  )
}

export default Portfolio
