import Project from "./Project";
import s from '@m/Portfolio/Projects.module.scss';
import gallery from '@assets/images/projects/project_gallery.png';
import dogs from '@assets/images/projects/project_dogs.png';
import dervish from '@assets/images/projects/project_dervish.png';
import notes from '@assets/images/projects/project_notes.png';
import quiz from '@assets/images/projects/project_quiz.png';
import { v4 } from 'uuid';

const data = [
  {
    title: 'Notes App',
    text: `Notes app will increase your
          productivity by letting you
          to add notes in in.`,
    imgRef: notes,
    codeRef: 'https://github.com/dalerfrontend/Notes',
    siteRef: 'https://dalerfrontend.github.io/notes/'
  },
  {
    title: 'Quiz',
    text: `Simple quiz app handlcrafted
           React, Redux and styled components`,
    imgRef: quiz,
    codeRef: 'https://github.com/dalerfrontend',
    siteRef: 'https://dalerfrontend.github.io/react4/'
  },
  {
    title: 'Dervish',
    text: `It is also fully responsive
          website with beautiful pictures
          of Dervish hostel`,
    imgRef: dervish,
    codeRef: 'https://github.com/dalerfrontend/dalerfrontend.github.io/tree/main/css5',
    siteRef: 'https://dalerfrontend.github.io/css5/'
  },
  {
    title: 'Dogs shop',
    text: `A fully responsible with amazing
          design dop shop made using only
          HTML, CSS and VueJS!`,
    imgRef: dogs,
    codeRef: 'https://github.com/dalerfrontend/dalerfrontend.github.io/tree/main/css8',
    siteRef: 'https://dalerfrontend.github.io/css8/'
  },
  {
    title: 'Gallery',
    text: `It might look simple but will
          amaze you with it's beauti. This
          is a collectoin of images. PS
          it is only suitable for desktop
          devices!`,
    imgRef: gallery,
    codeRef: 'https://github.com/dalerfrontend/dalerfrontend.github.io/tree/main/css4',
    siteRef: 'https://dalerfrontend.github.io/css4/'
  },
];

function Projects() {
  const renderedProjects = data.map(p => (
    <Project
      key={v4()}
      title={p.title}
      imgRef={p.imgRef}
      codeRef={p.codeRef}
      siteRef={p.siteRef}
    >
      {p.text}
    </Project>
  ));

  return (
    <div className={s['Projects']}>

      {/* Projects */}
        {renderedProjects}
      {/* Projects */}

    </div>
  )
}

export default Projects
