import s from '@m/Alert.module.scss';
import { AlertContext, ShadeContext, ThemeContext } from 'context';
import { useContext } from 'react';
import Shade from './Shade';

function Alert(props) {
  const themeContext = useContext(ThemeContext);

  const hideAlert = () => {
    props.alert.hide();
  };

  return (
    <>
      <Shade
        isVisible={props.alert.isVisible}
        onShadeClick={() => false}
      />
      <div className={`${s['Alert']} ${s[props.alert.isVisible ? 'visible' : '']}`}>

        {/* Title */}
          <div className={s['title']}>
            Thank you!
          </div>
        {/* Title */}

        {/* Text */}
          <div className={s['text']}>
            {`Your review will help us a lot
            to improve my cv`}
          </div>
        {/* Text */}

        {/* Ball */}
          <div className={s['ball']}>
            <div className={s['icon-container']}>
              <i className={`fas fa-check ${s['icon-check']}`}></i>
            </div>
          </div>
        {/* Ball */}

        {/* Close button */}
          <button
            onClick={hideAlert}
            className={`${s['close-btn']} ${s[themeContext.theme]}`}
          >
            Close
          </button>
        {/* Close button */}

      </div>
    </>
  )
}

export default Alert
