import { Link, useLocation } from 'react-router-dom'
import s from '@m/Header/Header.module.scss'
import { useContext } from 'react'
import { ThemeContext } from '../../context'
import withLang from '../withLang';
// import Nav from './Nav';

function Nav({ isBottom }) {
  return (
    <nav className={`${s['nav']} ${s[isBottom ? 'nav-bottom' : '']}`}>
      <ul>
        <NavLink path={'/home'}>
          Home
        </NavLink>
        <NavLink path={'/portfolio'}>
          Portfolio
        </NavLink>
        {/* <NavLink path={'/questions'}>
          Questions
        </NavLink> */}
      </ul>
    </nav>
  );
}

function NavLink({ path, children }) {
  const { pathname } = useLocation();

  return (
    <li className={`${s['nav-link']} ${s[pathname === path ? 'active': '']}`}>
      <Link to={path}>
        {children}
      </Link>
      <div className={s['link-underline']}></div>
    </li>
  );
}

function Header(props) {
  const themeContext = useContext(ThemeContext);

  const openSettings = () => {
    props.settings.open();
  };

  return (
    <div className={s['wrapper']}>
      <header className={s['Header']}>
        
        {/* Main part */}
          {/* <div className={s['main-part']}> */}

            <div className={s['logo']}>
              <span className={s['logo-text']}>
                frontendDaler
              </span>
              <div
                className={`${s['logo-rect']} ${s[themeContext.theme]}`}
              ></div>
            </div>

            <Nav />

            <div className={`${s['settings']} ${s[themeContext.theme]}`} onClick={openSettings}>
              <i className="fas fa-cog"></i>
            </div>

          {/* </div> */}
        {/* Main part */}
      </header>
      <Nav isBottom={true} />
    </div>
  )
}

const langs = {
  en: {
    'link-1': 'Main',
    'link-2': 'Portfolio',
    'link-3': 'Questions'
  },
  ru: {
    'link-1': 'Главная',
    'link-2': 'Портфолио',
    'link-3': 'Вопросы'
  }
}

export default withLang(Header);
