import s from '@m/MessageForm.module.scss';
import { sendMessage } from 'firebase/features';
import { useState } from 'react';
import Shade from './Shade';

function BigIcon(props) {
  return (
    <div className={s['big-icon']}>
      <div className={`fab fa-telegram ${s['icon-telegram']}`}></div>
    </div>
  );
}

function MessageForm(props) {
  const [form, setForm] = useState({
    from: '',
    phone: '',
    message: ''
  });

  const handleInputChange = (e) => {
    setForm({
      ...form,
      [e.target.name]: e.target.value
    })
  };

  const handleSendMessage = (e) => {
    const { from, phone, message } = form;
    e.preventDefault();
    sendMessage(form);
    props.message.hide();
    if (!from.trim()) {
      
    } else if (!phone.trim()) {

    } else if (!message.trim()) {

    } else {
      
    }
  };

  const handleCloseForm = () => {
    props.message.hide();
  };

  return (
    <>
      <Shade
        isVisible={props.message.isVisible}
        onShadeClick={() => false}
      />
      <form
        className={`${s['MessageForm']} ${s[props.message.isVisible ? 'visible' : '']}`}
      >
        <BigIcon />
        <h3 className={s['title']}>
          Send Message
        </h3>

        {/* From field */}
          <input
            name="from"
            className={s['field-from']}
            placeholder="Your name"
            value={form.from}
            onChange={handleInputChange}
          />
            {/* <div className={s["input-icon"]}>
              <i className="fas fa-user"></i>
            </div> */}
        {/* From field */}

        {/* Phone field */}
          <input
            name="phone"
            className={s['field-phone']}
            placeholder="Your phone number"
            value={form.phone}
            onChange={handleInputChange}
          />
            {/* <div className={s["input-icon"]}>
              <i className="fas fa-phone"></i>
            </div> */}
        {/* Phone field */}

        {/* Message Field */}
          <textarea
            name="message"
            value={form.text}
            className={s['field-message']}
            placeholder="Message"
            onChange={handleInputChange}
          >
          </textarea>
        {/* Message Field */}

        {/* Buttons */}
          <div className={s['buttons']}>

            {/* Send button */}
              <button
                className={s['send-btn']}
                onClick={handleSendMessage}
              >
                Send
              </button>
            {/* Send button */}

            {/* Close button */}
              <button
                className={s['close-btn']}
                onClick={handleCloseForm}
                type="button"
              >
                Close
              </button>
            {/* Close button */}

          </div>
        {/* Buttons */}

      </form>
    </>
  )
}

export default MessageForm
