const { useState } = require("react");

function useShade() {
  const [isVisible, setIsVisible] = useState(false);
  const [isHideOnClick, setIsHideOnClick] = useState(true);
  const [callback, setCallback] = useState(null);
  
  return {
    isVisible: isVisible,
    isHideOnClick: isHideOnClick,
    callback: callback,
    show: (hideOnClick = false) => {
      setIsVisible(true)
      setIsHideOnClick(hideOnClick);
    },
    hide: () => {
      setIsVisible(false);
      if (callback) {
        callback();
      }
    },
    changeCallback: (to) => setCallback(to), 
    toggle: () => setIsVisible(!isVisible)
  };
}

export default useShade;
