import styled from 'styled-components';

const StyledTitle = styled.div`
  text-align: center;
  width: 100%;
  font-size: 40px;
  margin: 0 auto 30px;
  line-height: 1;
  font-weight: ${({ isBald }) => isBald ? '500' : '400'};

  @media (max-width: 445px) {
    font-size: 36px;
  }
`;

export default StyledTitle;
